
import { useState } from 'react'
// import DetailBox from '../DetailBox/DetailBox';
import FormCss from './Form.module.css'




export default function Form(props: any) {
    const [currformData, setCurrFormData] = useState({
        email: '',
        firstName: '',
        lastName: ''
    });



    const [isValid, setIsValid] = useState({
        isValidPassword: false,
        isValidName: false
    })


    let formHandler = (e: any) => {

        if (e.target.name === 'email') {

            setCurrFormData({ ...currformData, email: e.target.value })
            // props.setFormData({ ...props.formData, email: e.target.value })
            if (validEmail(e.target.value)) {

                setIsValid({ ...isValid, isValidPassword: true })
            } else {

                setIsValid({ ...isValid, isValidPassword: false })

            }
        } else if (e.target.name === 'firstName') {

            setCurrFormData({ ...currformData, firstName: e.target.value })

            // props.setFormData({ ...props.formData, firstName: e.target.value })
            if (e.target.value.length > 2) {
                setIsValid({ ...isValid, isValidName: true })


            }
            else {
                setIsValid({ ...isValid, isValidName: false })

            }

        } else {

            setCurrFormData({ ...currformData, lastName: e.target.value })
            
            // props.setFormData({ ...props.formData, lastName: e.target.value })

        }

    }

    function validEmail(str: any) {
        let regx = /^\w+([\.-]?\w+)*@\w+([\.-]?\w+)*(\.\w{2,3})+$/
        if (regx.test(str)) return true
        else return false
    }


    let submitHandler = () => {



        props.setFormData({...currformData})

        
        const updateUser=[
            ...props.storeArray,{

                email: currformData.email,
                firstName: currformData.firstName,
                lastName: currformData.lastName
            }

        ]
  
        
    
        props.setStoreArray(updateUser)
       
        
      
        
     

        

    }



    return (
        <div className={FormCss.formWrapper}>
            <div className={FormCss.formBox}>
                <div className={FormCss.formItem}>

                    <label htmlFor="email">email: </label>
                    <input type="email" name="email" id="email" 
                    
                    value={currformData.email} 
                    // value={props.formData.email} 
                    
                    onChange={formHandler} />
                    {!isValid.isValidPassword && <p style={{marginBottom:'5px',color:'red'}}>enter a vaid email</p>
                    }
                </div>

                <div className={FormCss.formItem}>
                    <label htmlFor="firstName">firstName: </label>
                    <input type="text" name="firstName" id="firstName" 
                    
                    value={currformData.firstName}
                    // value={props.formData.firstName}
                    
                    onChange={formHandler} />
                    {!isValid.isValidName && <p style={{marginBottom:'5px',color:'red'}}>enter atleast 3 character</p>}

                </div>
                <div className={FormCss.formItem}>
                    <label htmlFor="lastName">lastName: </label>
                    <input type="text" name="lastName" id="lastName" 
                    
                    value={currformData.lastName} 
                    // value={props.formData.lastName} 
                    
                    onChange={formHandler} />
                </div>

                <button className={FormCss.submitBtn} onClick={submitHandler}>submit</button>
              

            </div>
        </div>
    )
};
